
.formConteiner{
 
   background-color: rgba(47, 44, 44, 0.65);
   font-size: medium;

   display: flex;
   flex-direction: row;
   justify-content: center;
   
   margin-top: 30px; 
   margin-bottom: 30px; 
   padding-bottom: 30px;
   padding-top: 30px;



}

.inputButton{
    color: white;
    font-size: 15px;
    font-weight: bold;
    width: 150px;
    
    background-color: transparent;
    padding: 5px;
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
   
    border-width: 1px;
    border-color: white;
    border-radius: 3px;
        
    cursor: pointer;
}


.inputButton option {
    font-weight:normal;
    background-color: black;
    width: 100%;
    height: 100%;
    border-radius: 3px;
  }

.createButton{
    color: white;
    font-size: 15px;
    font-weight: bold;
    text-decoration: none;
    width: 30%;
    

    background-color: transparent;
    padding: 10px;
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 35px;
  
    border-width: 1px;
    border-color: white;
    border-radius: 3px;
        
    cursor: pointer;
}

.createButton:hover{
    background-color:rgba(138, 138, 121, 0.356); 
}

.descConteiner{
   width: 250px;         
}

.danger{
    color: rgb(139, 8, 8);
}

.selection{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}